<template>
	<div class="container" style="position: absolute;left: 0;right: 0;margin: auto;">
		<table class="table table table-bordered">
			<tbody>
				<tr>
					<th>id</th>
					<th>姓名</th>
					<th>年龄</th>
					<th>性别</th>
					<th>班级</th>
					<th>科目</th>
					<th>分数</th>
					<th>操作</th>
				</tr>
				<tr v-for="item in datas">
					<td v-text="item.id"></td>
					<td v-text="item.name"></td>
					<td v-text="item.age"></td>
					<td v-text="item.sex"></td>
					<td v-text="item.classes"></td>
					<td v-text="item.course"></td>
					<td v-text="item.num"></td>
					<td>
						<button class="btn btn-default" type="submit">删除</button>
						<a class="btn btn-default" @click="edit(item.id)" role="button">编辑</a>
					</td>
				</tr>
			</tbody>
		</table>
		<a href="#/add" class="btn btn-success" role="button">+</a>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				datas:[
					{id:1,age:12,name:"张三",sex:"男",classes:"01",course:"高数",num:80},
					{id:2,age:12,name:"张三",sex:"男",classes:"01",course:"高数",num:80},
					{id:3,age:12,name:"张三",sex:"男",classes:"01",course:"高数",num:80},
					{id:4,age:12,name:"张三",sex:"男",classes:"01",course:"高数",num:80}
				]
			}
		},
		methods:{
			edit(id){
				this.$router.push('/edit/'+id);
			}
		}
	}
</script>

<style scoped>
	td,th{
		text-align: center !important;
	}
	th{
		font-size: 18px;
	}
</style>


